import fetchJsonp from 'fetch-jsonp';

const key = 'fbe8b86eee5e4fff9d050cd70c92b0f8';

function getWeather() {
  fetchJsonp(`https://api.darksky.net/forecast/${key}/56.50,60.35? exclude=currently`)
  .then(res => res.json())
  .then(data => {
    console.log(data);
    const output = document.querySelector('.nav-weather');
    output.innerHTML = `
      <h6 class="weather-status font-black">Сегодня:</h6>
      <i class="weather-icon font-black ${data.currently.icon}"></i>
      <h6 class="weather-temp font-red">${((data.currently.temperature - 32) / 1.8).toFixed(1)} °C</h6>
    `; 
  })
  .catch(err => console.log(err));
}

getWeather();